.header
  display: flex
  width: inherit
  background-color: #cdcdcd
  text-align: left
  .header-text
    margin-left: 10px

.adding-notes
  display: flex
  flex-direction: column
  align-items: center
  margin-top: 15px
  width: 100%
  .adding-form
    display: flex
    flex-direction: column
    align-items: flex-end
    width: 40%
    .add-button
      margin-top: 10px

.post-section
  display: flex
  width: inherit
  justify-content: center
  .post-content
    display: grid
    grid-template-columns: 7fr 1fr
    grid-gap: 10px
    margin-bottom: 10px
    background-color: #cdcdcd
    width: 40%
    margin-top: 10px
    padding: 10px
    text-align: left
    white-space: pre-wrap
    .button-keeper
      display: flex
      justify-content: center
      align-items: center

.detail-content
  display: flex
  width: 100%
  justify-content: center
  margin-top: 10px
  .details-content-div
    display: flex
    flex-direction: column
    width: 40%
    .details-content-buttons
      display: flex
      width: 100%
      justify-content: space-between
    .detail-content-description
      margin-top: 10px
      background-color: #cdcdcd
      white-space: pre-wrap
      text-align: left
      padding-left: 10px
      padding-right: 10px
      padding-bottom: 10px
